// Typography
// ----------

$link: '&:link, &:visited';
$focus: '&:hover, &:focus, &:active';


@mixin ellipsis($no-wrap: true) {
  @if $no-wrap { white-space: nowrap; }

  overflow: hidden;
  text-overflow: ellipsis;
}


%h1 {
  @extend %serif;
  @include font-size(large, 2);
}

%h2 {
  @extend %serif;
  text-transform: uppercase;
}

%h3 {
  @extend %serif;
}

%h4 {
  @extend %serif;
}

%link {
  &:link,
  &:visited {
    color: color(action);
    text-decoration: none;
  }

  &:hover,
  &:active,
  &:focus {
    color: color(focus);
    text-decoration: underline;
  }
}

%body {
  h1 {
    @extend %h1;
  }

  h2 {
    @extend %h2;
  }

  h3 {
    @extend %h3;
  }

  h4 {
    @extend %h4;
  }

  strong {
    font-weight: bold;
  }

  em {
    @extend %caps;
  }

  p,
  ul,
  ol,
  pre,
  blockquote,
  figure {
    margin-bottom: size(gutter);
  }

  p:empty {
    display: none;
  }

  ul {
    list-style-type: disc;
  }

  ol {
    list-style-type: decimal;
  }

  ul,
  ol,
  blockquote {
    ul,
    ol {
      margin-left: size(gutter);
    }
  }
}
